.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.flex {
  display: flex;
  
  &--column {
    flex-flow: column;
  }

  &--column-reverse {
    flex-flow: column-reverse;
  }
}

.align-center {
  align-items: center;
}

.text-center {
  text-align: center;
}

@for $i from 1 through 12 {
  .large-#{$i} {
    width: #{($i / 12) * 100 + '%'};
  }

  .space-bottom-#{$i} {
    margin-bottom: #{$i + 'rem'};
  }

  .space-top-#{$i} {
    margin-top: #{$i + 'rem'};
  }
}
